.home-main-wrapper{
    width: 60%;
    margin: 1% auto;
    display: flex;
}

.home-main-wrapper>img{
    border-radius: 50%;
    height:500px;
    
}

.home-main-wrapper>div>h1{
    font-size: 5em;
    margin: 0 0 0 -25px;
    color: #80d8da;
}
.home-main-wrapper>div>h2{
    font-size: 3.5em;
    margin: 0 0 8px 10px;
    color: #f34424;
}
.home-main-wrapper>div>p{
    font-size: 1.140em;
    margin: 0 0 0 30px;
    color: white;
}

.nav-buttons-container{
    display: flex;
    margin-top: 30px;
    width: 100%;
    justify-content: space-between;
}
.nav-buttons{
    font-size: 1.5em;
    text-align: center;
    line-height: 120px;
    width: 130px;
    height: 130px;
    border-radius: 50%;
    transition: all .15s ease-in;
    color: black;
    text-decoration: none;
}
.nav-buttons:hover{
    background-color: white!important;
}
.nav-buttons:nth-child(1){
    background-color:#80d8da;
}
.nav-buttons:nth-child(2){
    background-color:#f34424;
}
.nav-buttons:nth-child(3){
    background-color: #eea32d;
}

