.work-experience, .education, .skills-expertise{
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin: auto;
}
.work-experience>div, .education>div, .skills-expertise>div{

    width: 70%;
}
.entry{
    display: flex;
    width: 100%;
    text-align: left;
}
.entry>ul{
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.entry>p{
    width: 150px;
    min-width: 150px;
}
