@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
.header-wrapper{
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: "Lato";
    font-size: 1.3em;
    color: white;
    background-color: #80d9daa9;
}
.header-wrapper>div{
    display: flex;
    width: 25%;
    justify-content: space-evenly;
    padding-left: 1%;
}
.position {
    font-size: 1em;
    color: #f34324e1;
}

.header-wrapper>ul{
    display: flex;
    justify-content: space-evenly;
    width: 25%;
    list-style-type: none;
    padding-right: 5%;
}
.header-nav-links{
    color:white;
    text-decoration: none;
}